<template>
  <v-container class="mt-4">
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="primary" dark @click="$router.push('/custom/registration_view?path=/itsf&role=' + queries.role + '&practitioner=' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Registration</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-alert type="primary" v-if="fullname">
      <center><b>Paying Fees For {{ fullname }}</b></center>
    </v-alert>
    <v-form id="app" ref="form" v-model="valid">
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
          Fees Payment Information
        </v-card-title>
        <v-card-text class="my-3" v-if="!overlay">
          <fhir-integer field="amount" :initial="amount" :slotProps="{ source: { path: 'amount', data: [] }}" :edit="true" path="amount" displayCondition="" id="Basic.extension:amount.value[x]:valueInteger" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:amount.value[x]:valueInteger" label="Paid Amount" min="1" max="1"></fhir-integer>
          <fhir-attachment field="receipt" :initial="receipt" :slotProps="{ source: { path: 'amount', data: [] }}" :edit="true" path="receipt" displayCondition="amount|!=|0" id="Basic.extension:receipt.value[x]:valueAttachment" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:receipt.value[x]:valueAttachment" label="Payment Receipt" min="1" max="1"></fhir-attachment>
          <fhir-attachment field="exemption" :initial="exemption" :slotProps="{ source: { path: 'exemption', data: [] }}" :edit="true" path="exemption" displayCondition="amount|=|0" id="Basic.extension:exemption-letter.value[x]:valueAttachment" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:exemption-letter.value[x]:valueAttachment" label="Exemption Letter" min="1" max="1"></fhir-attachment>
          <fhir-date field="payment_date" :initial="payment_date" :slotProps="{ source: { path: 'payment_date', data: [] }}" :edit="true" path="payment_date" displayCondition="" id="Basic.extension:date.value[x]:valueDate" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:date.value[x]:valueDate" minValueDate="1920-01-01" maxValueDate="2030-01-01" label="Payment Date" min="1" max="1"></fhir-date>
        </v-card-text>
      </v-card>
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-actions>
          <v-row>
            <v-col>
              <v-btn class="error" dark @click="$router.push('/custom/registration_view?path=/itsf&role=' + queries.id + '&practitioner=' + queries.practitioner)">
                <v-icon light left>mdi-cancel</v-icon>
                <span>{{ $t("App.hardcoded-texts.Cancel") }}</span>
              </v-btn>
            </v-col>
            <v-spacer></v-spacer>
            <v-col>
              <v-btn
                v-if="valid"
                :disabled="!valid"
                class="success darken-1"
                dark
                @click="save()"
              >
                <v-icon light left>mdi-content-save</v-icon>
                <span>{{ $t("App.hardcoded-texts.Save") }}</span>
              </v-btn>
              <v-btn v-else class="green" dark @click="$refs.form.validate()">
                <v-icon light left>mdi-content-save</v-icon>
                <span>{{ $t("App.hardcoded-texts.Save") }}</span>
              </v-btn>
            </v-col>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-container>
</template>

<script>
export default {
  props: ["queries"],
  data: function() {
    return {
      overlay: false,
      valid: true,
      amount: "",
      receipt: {},
      exemption: {},
      payment_date: "",
      fees_resource: {},
      registration_resource: {},
      fullname: ""
    }
  },
  methods: {
    save() {
      this.overlay = true;
      this.populateValues(this.$children)
      let index = this.fees_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-amount"
      })
      this.fees_resource.extension[index].valueInteger = this.amount
      index = this.fees_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-receipt"
      })
      if(this.receipt.data) {
        this.fees_resource.extension[index].valueAttachment = this.receipt
      } else {
        this.fees_resource.extension.splice(index, 1)
      }
      index = this.fees_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-exemption-letter"
      })
      if(this.exemption.data) {
        this.fees_resource.extension[index].valueAttachment = this.exemption
      } else {
        this.fees_resource.extension.splice(index, 1)
      }
      
      index = this.fees_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-date"
      })
      this.fees_resource.extension[index].valueDate = this.payment_date
      
      let bundle = {
        entry: [{
          resource: this.registration_resource,
          request: {
            method: "PUT",
            url: "PractitionerRole/" + this.registration_resource.id
          }
        }, {
          resource: this.fees_resource,
          request: {
            method: "PUT",
            url: "Basic/" + this.fees_resource.id
          }
        }]
      }
      fetch(`/site/student/editRegistration/${this.queries.practitioner}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(bundle),
      }).then((response) => {
        this.overlay = false;
        if(response.ok) {
          this.$store.commit("setMessage", {
            type: "success",
            text: this.$t("Fees Edited Successfully"),
          });
          this.$router.push(`/custom/registration_view?path=/itsf&role=${this.queries.role}&practitioner=${this.queries.practitioner}`)
        } else {
          response.json().then((response) => {
            let text = "Failed to edit fees."
            if(response.message) {
              text = response.message
            }
            this.$store.commit("setMessage", {
              type: "error",
              text: text
            });
          }).catch(() => {
            this.$store.commit("setMessage", {
              type: "error",
              text: this.$t("Failed to edit fees")
            });
          })
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
      })
    },
    getFees() {
      return new Promise((resolve, reject) => {
        fetch("/fhir/Basic/" + this.queries.id).then(async(response) => {
          response.json().then(async (response) => {
            this.fees_resource = response
            this.amount = response.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-amount"
            })?.valueInteger
            this.receipt = response.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-receipt"
            })?.valueAttachment
            this.exemption = response.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-exemption-letter"
            })?.valueAttachment
            this.payment_date = response.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-payments-date"
            })?.valueDate
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })  
      })
    },
    getRole() {
      return new Promise((resolve, reject) => {
        fetch("/fhir/PractitionerRole/" + this.queries.role).then(async(response) => {
          response.json().then(async (response) => {
            this.registration_resource = response
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })
      })
    },
    getName() {
      this.$fhirutils.resourceLookup("Practitioner/" + this.queries.practitioner).then((response) => {
        this.fullname = response
      })
    },
    populateValues(children) {
      for (let child of children) {
        if(child.qField && child.path) {
          this[child.path] = child.value
        }
        if (child.$children) {
          this.populateValues(child.$children)
        }
      }
    }
  },
  components: {
    "fhir-integer": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-integer" ),
    "fhir-date": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-date" ),
    "fhir-attachment": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-attachment" ),
  },
  created() {
    this.overlay = true
    Promise.all([this.getFees(), this.getRole()]).then(() => {
      this.overlay = false
    }).catch(() => {
      this.overlay = false
    })
    this.getName()
  },
}
</script>